// src/components/BookingForm.js
"use client"; 

import { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

export default function BookingForm({ tour, onClose }) {
  const [startDate, setStartDate] = useState(null);
  const [guests, setGuests] = useState(1);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    alert(`Successfully booked ${tour.name} for ${guests} guests on ${startDate}`);
    onClose();
    setIsSubmitting(false);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block mb-1 font-medium">Travel Date</label>
        <DatePicker
          selected={startDate}
          onChange={(date) => setStartDate(date)}
          minDate={new Date()}
          className="border p-2 rounded w-full"
          required
        />
      </div>
      
      <div>
        <label className="block mb-1 font-medium">Number of Guests</label>
        <input
          type="number"
          min="1"
          max="10"
          value={guests}
          onChange={(e) => setGuests(parseInt(e.target.value))}
          className="border p-2 rounded w-full"
          required
        />
      </div>
      
      <div className="flex gap-2 pt-2">
        <button
          type="button"
          onClick={onClose}
          className="flex-1 border border-gray-300 text-gray-700 px-4 py-2 rounded"
        >
          Cancel
        </button>
        <button
          type="submit"
          disabled={isSubmitting}
          className={`flex-1 bg-green-600 text-white px-4 py-2 rounded ${isSubmitting ? 'opacity-70' : 'hover:bg-green-700'}`}
        >
          {isSubmitting ? 'Processing...' : 'Confirm Booking'}
        </button>
      </div>
    </form>
  );
}